




















































































/* eslint-disable @typescript-eslint/no-non-null-assertion */

import { Component, Vue, Watch } from 'vue-property-decorator';
import SwordIcon from 'vue-material-design-icons/Sword.vue';
import HeartIcon from 'vue-material-design-icons/Heart.vue';
import DramaMasksIcon from 'vue-material-design-icons/DramaMasks.vue';
import HeadSyncOutlineIcon from 'vue-material-design-icons/HeadSyncOutline.vue';
import ShieldHalfFullIcon from 'vue-material-design-icons/ShieldHalfFull.vue';
import ChessKnightIcon from 'vue-material-design-icons/ChessKnight.vue';
import 'vue-material-design-icons/styles.css';
import { Card } from "@/types";

interface HTMLInputEvent extends Event {
    target: HTMLInputElement & EventTarget;
}

@Component({
    components: {
        SwordIcon,
        HeartIcon,
        DramaMasksIcon,
        HeadSyncOutlineIcon,
        ShieldHalfFullIcon,
        ChessKnightIcon
    }
})
export default class Home extends Vue {
    private cardData: Partial<Card> = {};
    private fontSize = 12;
    private importError = "";

    mounted(): void {
        const storageVal = localStorage.getItem("cardData");
        if (storageVal) {
            const parsed = JSON.parse(storageVal);
            delete parsed.imageURL;
            this.cardData = parsed;
        }
    }

    fixFontSize(): void {
        const canvas = document.createElement("canvas");
        const context = canvas.getContext("2d")!;
        context.font = `15px RobotoSlab`;
        const metrics = context.measureText(`"${this.cardData.quote}"`);
        const totalWidth = metrics.width;
        const lineCount = Math.ceil(totalWidth / 180);

        const newFontSize = Math.ceil(60 / 1.15 / lineCount);
        if (newFontSize <= 12) {
            this.fontSize = newFontSize;
        }
    }

    uploadImage(e: HTMLInputEvent): void {
        const file = e.target?.files?.[0];
        if (!file) {
            return;
        }
        const objectUrl = window.URL.createObjectURL(file);
        console.log(objectUrl);
        this.cardData.imageURL = objectUrl;
        this.$forceUpdate();
    }

    handlePaste(e: ClipboardEvent): void {
        const file = e.clipboardData?.files?.[0];
        if (!file) {
            return;
        }
        const objectUrl = window.URL.createObjectURL(file);
        console.log(objectUrl);
        this.cardData.imageURL = objectUrl;
        this.$forceUpdate();
    }

    handleImport(e: InputEvent): void {
        try {
            const parsed = JSON.parse(e.data || "{}");
            console.log(parsed);
            this.cardData = parsed;
        } catch (e) {
            this.importError = e;
        }
    }

    get invokerFile(): string {
        const invoker = this.cardData.invoker;
        console.log(invoker);
        if (invoker === "anyone" || !invoker) {
            return require(`@/assets/cc-logo-bw.png`);
        }
        return require(`@/assets/collections/${invoker}.png`);
    }

    @Watch("cardData", { deep: true })
    onPropertyChanged(): void {
        const setCardData = this.cardData;
        localStorage.setItem("cardData", JSON.stringify(setCardData));
    }
}
